.arrow{
  width: 40px;
  height: 40px;
  position: relative;
  display: inline-block;
  margin: 40px/4 40px/4;  
}

.arrow:before, .arrow:after {
    content:'';
    border-color:transparent;
    border-style:solid;
    position: absolute;
  }

  .arrow .curve {    
    position: absolute;
    overflow: hidden;
  }

  .arrow .curve::before {
    content:'';
    position:absolute;
  }


/* // * * * ARROW ANIMATE * * * (Just for fun) */
.arrow-animate{
  -webkit-animation: spin 2s infinite linear;
  margin-left:40px/2;
    
  height: 40px;
  width: 40px;
  /*
    i{
    @include border-radius(2000px);
    position: absolute;
    top: 40px*.115;
    left:40px*.115;
    width: 40px*.57;
    height: 40px*.57;
    border:40px*.1 solid rgba(255,0,0,.5);
    z-index: 1000;
  }*/
}
  .arrow-animate .arrow-item{
    width: 40px;
    height: 40px*.5;
    position: absolute;
  }
    .arrow-animate .arrow-item:before{
      content:'';
      height: 0;
      width: 0;
      position: absolute;
      border:40px*.18 solid transparent;
    }

    .arrow-animate .arrow-item .curve{
       height: 40px*.42;
       width: 40px;
        /* //background-color: rgba(255,0,0,.2); */
    }
        .arrow-animate .arrow-item .curve:before{
         /* @include border-radius(2000px); */
         border:40px*.1 solid #555;
         height:40px*.57;
         width:40px*.57;
         left:40px*.115;
    
       }
    
    .arrow-animate .arrow-item .curve:first-child{
      top:0;
      left:0;
    }
      .arrow-animate .arrow-item .curve:first-child:before{
        border-top-color:#555;
        bottom: -40px*.18;
      }
      .arrow-animate .arrow-item .curve:first-child .curve{
        top: 0;
        left: 0;}

        .arrow-animate .arrow-item .curve:first-child .curve:before{
          top: 40px*.115;
        }
    
    
        .arrow-animate .arrow-item .curve:last-child{
       top:40px/2;
       left:0;
        }
       .arrow-animate .arrow-item .curve:last-child:before{
        border-bottom-color:#555;
        top: -40px*0.2;
        right:0;
      }
      .arrow-animate .arrow-item .curve:last-child:before .curve{
        bottom: 0;
        left: 0;
      }
        .arrow-animate .arrow-item .curve:last-child:before .curve:before{
          bottom: 40px*.115;
        }
      

